        email:'',
        message:''
        }
    }
    },
    methods: {
         showmsg(){
            var name=this.user.user
            var email= this.user.email
            var msg=this.user.message
            if ((/^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(this.user.email))!=true && name != '' && msg != '')
            {
                swal({
                title: "invalid Email!",
                text: "Please check the Email-id and try again",
                icon: "error",
                button: "Ok",
                });
            }
            else{
                        if(name == '' || email == '' || msg == '')
                    {
                        swal({
                        title: "Fields Empty!",
                        text: "Please check the missing fields",
                        icon: "warning",
                        button: "Ok",
                        });
                    }
                    else{
                            console.log(this.user.email,this.user.email,this.user.message)
